import React from "react";
import { Profile } from "./Profile";
import { connect } from "react-redux";
import { onProfile, getProfileStatus, putProfileStatus } from "../redux/ProfileReducer";
import { useParams } from 'react-router-dom';
import { withAuthNavigate } from "../hoc/withAuthNavigate";
import { compose } from "redux";
function withRouter(Children) {
    return (props) => {
        const match = { params: useParams() };
        return <Children {...props} match={match} />
    }
}
class ProfileContainerComponent extends React.Component {
    componentDidMount() {
        let profileId = this.props.match.params.profileId
        if (!profileId) {
            profileId = this.props.myUserId
            // if (!profileId) {
            //     this.props.history.push("/login")
            // }
        }
        this.props.onProfile(profileId)
        this.props.getProfileStatus(profileId)
    }
    render() {
        return (
            <Profile {...this.props} profile={this.props.profile} status={this.props.status} putProfileStatus={this.props.putProfileStatus} />
        )
    }
}
let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    myUserId: state.auth.userId,
    isAuth: state.auth.isAuth
})

export const ProfileContainer = compose(
    connect(mapStateToProps, { onProfile, getProfileStatus, putProfileStatus }),
    withRouter,
    withAuthNavigate
)(ProfileContainerComponent)