import React from "react";
import { Formik, Field, Form } from "formik";
export const PageOneForm = (props) => {
    return <>
        <Formik
            initialValues={{
                addMessage: ""
            }}
        // onSubmit={handleSubmit}
        >
            <Form>
                <Field type="text"
                    value={props.messages.messegeValue}
                    onChange={props.onChangeAddMessege}
                    name="addMessage"
                />
                <button type="submit" onClick={props.onAddMessage}>ADD MASSAGE</button>
            </Form>
        </Formik>
    </>
}