export const validateForm = {
    validateEmailForLogin(value) {
        let error;
        if (!value) {
            error = 'Обов\'язкове поле';
        } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)) {
            error = 'Це не формат електронної пошти';
        }
        return error;
    },
    validatePasswordForLogin(minLength, maxLength) {
        return (value) => {
            {
                let error;
                if (!value) {
                    error = 'Обов\'язкове поле';
                } else if (value.length > maxLength) {
                    error = `Пароль має бути не більше ${maxLength} символів`
                } else if (value.length < minLength) {
                    error = `Пароль має бути не менше ${minLength} символів`
                }
                return error;
            }
        }
    }
}