import { applyMiddleware, combineReducers, compose, legacy_createStore as createStore } from "redux";
import ThunkMiddleware from "redux-thunk";
import { MessagesReducer } from "./components/redux/MessagesReducer";
import { NamesReducer } from "./components/redux/NamesReducer";
import { NewUserFriendReducer } from "./components/redux/NewFriendReducer";
import { ProfileReducer } from "./components/redux/ProfileReducer";
import { AuthReducer } from "./components/redux/AuthReducer";
import { AppReducer } from "./components/redux/AppReducer";

const reducerContainer = combineReducers({
  users: NamesReducer,
  messages: MessagesReducer,
  newUserFriend: NewUserFriendReducer,
  profilePage: ProfileReducer,
  auth: AuthReducer,
  initializeApp: AppReducer
});
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducerContainer, composeEnhancers(
  applyMiddleware(ThunkMiddleware)
));


// const store = createStore(reducerContainer, applyMiddleware(ThunkMiddleware));
// window.store = store;
export default store;
