import React, { useState, useEffect } from "react";

export const ProfileStatusWithHooks = (props) => {
    const [editMode, setEditMode] = useState(false)
    const [status, setStatus] = useState(props.status)
    useEffect(() => {
        setStatus(props.status)
    }, [props.status])
    const statusChange = (event) => {
        setStatus(event.target.value)
    }
    const activedEditMode = () => {
        setEditMode(true)
    }
    const deActivedEditMode = () => {
        setEditMode(false)
        props.putProfileStatus(status)
    }
    const onPressEnter = (event) => {
        if (event.key === "Enter") {
            deActivedEditMode()
        }
    }
    return (
        <>
            {!editMode ?
                <div>
                    <span style={{ fontSize: "50px", cursor: "pointer" }} onClick={activedEditMode}>
                        {<div>{props.status || <button>Add Status</button>}</div>}
                    </span>
                </div >
                :
                <div>
                    <input onKeyDown={onPressEnter} style={{ fontSize: "50px", padding: "0px", fontFamily: "inherit" }} autoFocus onChange={statusChange} onBlur={deActivedEditMode} value={status}></input>
                    <div><button onClick={deActivedEditMode}>Save Status</button></div>
                </div >
            }
        </>
    )
}