const ADD_MESSEGE = "MessagesReducer/ADD-MESSEGE";
const ON_CHANGE_ADD_MESSEGE = "MessagesReducer/ON-CHANGE-ADD-MESSEGE";
const intialState = {
  messeges: [
    {
      message: "Привіт як зліви?",
      id: 0,
    },
  ],
  messegeValue: "Hello",
};
export const MessagesReducer = (state = intialState, action) => {
  switch (action.type) {
    case ADD_MESSEGE:
      const addNewMessege = {
        message: state.messegeValue,
        id: state.messeges[state.messeges.length - 1].id + 1,
      };
      return {
        ...state,
        messeges: [...state.messeges, addNewMessege],
        messegeValue: "",
      };
    case ON_CHANGE_ADD_MESSEGE:
      return {
        ...state,
        messegeValue: action.newMessege,
      };
    default:
      return state;
  }
};
export const addMessegeActionCreater = () => {
  return { type: ADD_MESSEGE };
};
export const onChangeAddMessegeActionCreater = (createMessege) => {
  return { type: ON_CHANGE_ADD_MESSEGE, newMessege: createMessege };
};
