import { usersAPI } from "../api/api"

const FOLLOW = "NewUserFriendReducer/FOLLOW"
const SET_USERS = "NewUserFriendReducer/SET-USERS"
const SET_SELECTED_PAGE = "NewUserFriendReducer/SET-SELECTED-PAGE"
const SET_TOTAL_COUNT = "NewUserFriendReducer/SET-TOTAL-COUNT"
const TOGGLE_IS_FETCHING = "NewUserFriendReducer/TOGGLE-IS-FETCHING"
const TOGGLE_IS_FLOAD = "NewUserFriendReducer/TOGGLE_IS_FLOAD"
let initialState = {
    users: [],
    pageSize: 5,
    totalUsersCount: 0,
    selectedPage: 1,
    isFetching: false,
    followLoad: []
}
export const NewUserFriendReducer = (state = initialState, action) => {

    switch (action.type) {
        case TOGGLE_IS_FETCHING:
            return { ...state, isFetching: action.isFetching }
        case TOGGLE_IS_FLOAD:
            return {
                ...state, followLoad: action.isFetching
                    ? [...state.followLoad, action.userId]
                    : state.followLoad.filter(id => id !== action.userId)
            }
        case FOLLOW:
            return {
                ...state,
                users: state.users.map(users => {
                    if (users.id === action.userId) {
                        return {
                            ...users, followed: !users.followed
                        }
                    }
                    return users
                }
                )
            }
        case SET_USERS:
            return { ...state, users: action.users }
        case SET_SELECTED_PAGE:
            return { ...state, selectedPage: action.selectedPage }
        case SET_TOTAL_COUNT:
            return { ...state, totalUsersCount: action.totalUsersCount }

        default: return state
    }
}

export const onFollow = (userId) => ({
    type: FOLLOW, userId
})
export const onSetUsers = (users) => ({
    type: SET_USERS, users
})
export const onSelectedPage = (selectedPage) => ({
    type: SET_SELECTED_PAGE, selectedPage
})
export const onSetTotalUsersCount = (totalUsersCount) => ({
    type: SET_TOTAL_COUNT, totalUsersCount
})
export const onFetching = (isFetching) => ({
    type: TOGGLE_IS_FETCHING, isFetching
})
export const onFollowingLoad = (isFetching, userId) => ({
    type: TOGGLE_IS_FLOAD, isFetching, userId
})
// getUsersThunkCreator
export const getUsers = (selectedPage, pageSize) => async (dispatch) => {
    dispatch(onFetching(true))
    dispatch(onSelectedPage(selectedPage))
    let data = await usersAPI.getUsers(selectedPage, pageSize)
    dispatch(onFetching(false))
    dispatch(onSetUsers(data.items))
    dispatch(onSetTotalUsersCount(data.totalCount))
}
// 
const FollowUnfollowFunction = async (id, dispatch, methodApi) => {
    dispatch(onFollowingLoad(true, id))
    let data = await methodApi(id)
    if (data.resultCode === 0) {
        dispatch(onFollow(id))
    }
    dispatch(onFollowingLoad(false, id))
}
export const follow = (id) => (dispatch) => {
    let methodApi = usersAPI.postFollow.bind(usersAPI)
    FollowUnfollowFunction(id, dispatch, methodApi)
}
export const unfollow = (id) => (dispatch) => {
    let methodApi = usersAPI.delFollow.bind(usersAPI)
    FollowUnfollowFunction(id, dispatch, methodApi)
}
