import { profileAPI } from "../api/api";
const SET_PROFILE = "ProfileReducer/SET-PROFILE";
const SET_STATUS = "ProfileReducer/SET_STATUS";

let initialState = {
  profile: [],
  status: ""
};

export const ProfileReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_PROFILE: {
      return { ...state, profile: action.profile };
    }
    case SET_STATUS: {
      return { ...state, status: action.status }
    }
    default:
      return state;
  }
};

const onSetProfile = (profile) => ({
  type: SET_PROFILE, profile,
})
export const onSetStatus = (status) => ({
  type: SET_STATUS, status,
})

// Thunk
export const onProfile = (profileId) => async (dispatch) => {
  let data = await profileAPI.getProfile(profileId)
  dispatch(onSetProfile(data))
}
export const getProfileStatus = (profileId) => async (dispatch) => {
  let data = await profileAPI.getProfileStatus(profileId)
  dispatch(onSetStatus(data))
}
export const putProfileStatus = (status) => async (dispatch) => {
  let data = await profileAPI.putProfileStatus(status)
  if (data.resultCode === 0) {
    dispatch(onSetStatus(status))
  }
}
