import React from "react";
import { NavLink } from "react-router-dom";
export const NavBar = ({ onChangeAddUser, onAddUser, users }) => {
    return (
        <div className="navbar">
            {[...users.names].reverse().map((item) => <NavLink key={item.id} to={`/${item.name.toLowerCase()}`}>{item.name}</NavLink >)}
            <input type="text" value={users.addUserValue} onChange={onChangeAddUser} />
            <button onClick={onAddUser}>add</button>
        </div>
    )
}