import { compose } from 'redux';
import { PageOne } from './PageOne'
import { withAuthNavigate } from '../hoc/withAuthNavigate';
import { addMessegeActionCreater, onChangeAddMessegeActionCreater } from "../redux/MessagesReducer";
import { connect } from "react-redux/es/exports";
const mapStateToProps = (state) => {
    return {
        messages: state.messages
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        onAddMessage: () => {
            dispatch(addMessegeActionCreater())
        },
        onChangeAddMessege: (e) => {
            let createMessege = e.target.value
            dispatch(onChangeAddMessegeActionCreater(createMessege))
        }
    }
}

export const PageOneContainer = compose(
    connect(mapStateToProps, mapDispatchToProps),
    withAuthNavigate
)(PageOne)