import { authAPI } from "../api/api"
const SET_USER_DATA = 'AuthReducer/SET_USER_DATA'


let initalState = {
    userId: "",
    email: "",
    login: "",
    isAuth: false
}

export const AuthReducer = (state = initalState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return { ...state, ...action.data }
        default:
            return state
    }
}

export const setAuthUserData = (userId, email, login, isAuth) => ({
    type: SET_USER_DATA, data: { userId, email, login, isAuth }
})
// thunk
export const onAuth = () => async (dispatch) => {
    let data = await authAPI.getAuth()
    if (data.resultCode === 0) {
        let { id, email, login } = data.data
        dispatch(setAuthUserData(id, email, login, true))
    }
}
export const onLogin = (email, password, rememberMe, setStatus) => async (dispatch) => {
    let data = await authAPI.postLogin(email, password, rememberMe)
    if (data.resultCode === 0) {
        dispatch(onAuth())
    } else {
        // debugger
        setStatus({ error: data.messages[0] })
    }
}
export const outLogin = () => async (dispatch) => {
    let data = await authAPI.delLogin()
    if (data.resultCode === 0) {
        dispatch(setAuthUserData("", "", "", false))
    }
}
