import React, { useState } from "react";
import { onLogin } from "../redux/AuthReducer";
import { Field, Form, Formik } from "formik";
import { connect } from "react-redux";
import { validateForm } from "../validateForm/validate";
import { customInputEmail } from "../validateForm/customInput";
import { Navigate } from "react-router-dom";

const LoginForm = (props) => {
    const [submitStatus, setSubmitStatus] = useState([]);
    let onPressEnterBoolean = false
    let onPressEnter = (event) => {
        if (onPressEnterBoolean === true) {
            if (event.key === "Enter") {
                event.click()
            }
        }
    }
    return <>
        <Formik
            initialValues={{
                email: "",
                password: "",
                toggle: false
            }}
            onSubmit={(values, submitProps) => {
                props.onLogin(values.email, values.password, values.toggle, (error) => {
                    setSubmitStatus(error); // Сохраняем статус в локальном состоянии
                    submitProps.setStatus(error); // Прокидываем статус в Formik
                });
            }}
        >
            {({ errors, touched }) => (
                <Form method="#" >
                    <div><Field type="email" placeholder="Email" name="email" onFocus={onPressEnterBoolean => true} validate={validateForm.validateEmailForLogin} component={customInputEmail} />
                    </div>
                    <div><Field type="password" placeholder="Password" onFocus={onPressEnterBoolean => true} name="password" validate={validateForm.validatePasswordForLogin(5, 30)} style={errors.password && touched.password && { border: "solid red 2px" }} />
                        {errors.password && touched.password && <div style={{ fontSize: "20px", color: "red" }}>{errors.password}</div>}
                    </div>
                    {submitStatus.error && <div style={{ fontSize: "20px", color: "red" }}>{submitStatus.error}</div>}
                    <label>
                        <Field type="checkbox" name="toggle" />
                        save
                    </label>
                    <div><button type="submit" onKeyDown={onPressEnter}>Submit</button></div>
                </Form>
            )}
        </Formik >
    </>
}

const LoginComponent = (props) => {
    if (props.isAuth) return <Navigate to={"/profile"}></Navigate>
    return <>
        <div style={{ fontSize: "30px" }}>Login Page</div>
        <LoginForm  {...props} />
    </>
}
const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})
export const Login = connect(mapStateToProps, { onLogin })(LoginComponent)