import React from "react";

const inputForm = (props) => { return <input {...props}></input> }
const textAreaForm = (props) => { return <textarea {...props}></textarea> }

const LowLost = ({ field, form: { touched, errors }, Element, ...props }) => {
    return <>
        <Element  type="text"{...field} {...props} style={touched.email && errors.email && { border: "solid red 2px" }} />
        {touched.email && errors.email && <div style={{ fontSize: "20px", color: "red" }}>{errors.email}</div>}
    </>
}

export const customInputEmail = (props) => {
    return <>
        <LowLost {...props} Element={inputForm}></LowLost>
    </>
}

export const customAreaEmail = (props) => {
    return <>
        <LowLost {...props} Element={textAreaForm}></LowLost>
    </>
}






