const ADD_USERS = 'NamesReducer/ADD-USERS'
const ON_CHANGE_ADD_USER = 'NamesReducer/ON-CHANGE-ADD-USER'
const DELETE_USERS = 'NamesReducer/DELETE_USERS'
const intialState = {
    names: [
        {
            name: "Massage",
            id: 0
        },
        {
            name: "Profile",
            id: 1
        },
        {
            name: "Friends",
            id: 2
        }
    ],
    addUserValue: 'Loxops'
}
export const NamesReducer = (state = intialState, action) => {
    switch (action.type) {
        case ADD_USERS:
            const newUser = {
                name: state.addUserValue,
                id: state.names[state.names.length - 1].id + 1
            }
            return {
                ...state,
                names: [...state.names, newUser],
                addUserValue: ""
            }
        case ON_CHANGE_ADD_USER:
            return { ...state, addUserValue: action.newText }
        case DELETE_USERS:
            return { ...state, names: state.names.filter(u => u.id !== action.id) }
        default:
            return state
    }
}

export const addUsersActionCreater = () => {
    return { type: ADD_USERS }
}
export const onChangeAddUserActionCreater = (text) => {
    return { type: ON_CHANGE_ADD_USER, newText: text }
}
export const deletePost = (id) => ({
    type: DELETE_USERS, id
})