import axios from "axios"

const instance = axios.create({
    withCredentials: true,
    baseURL: "https://social-network.samuraijs.com/api/1.0/",
    headers: { "API-KEY": "731d7d28-3f5e-4865-9ee8-6891e2dfeeee" }
})
const responsed = response => response.data

export const usersAPI = {
    getUsers(selectedPage, pageSize) {
        return instance.get(`users?page=${selectedPage}&count=${pageSize}`).then(responsed)
    },
    delFollow(id) {
        return instance.delete(`follow/${id}`).then(responsed)
    },
    postFollow(id) {
        return instance.post(`follow/${id}`).then(responsed)
    }
}
export const profileAPI = {
    getProfile(profileId) {
        return instance.get(`profile/` + profileId).then(responsed)
    },
    getProfileStatus(profileId) {
        return instance.get(`profile/status/` + profileId).then(responsed)
    },
    putProfileStatus(status) {
        return instance.put(`profile/status`, { status: status }).then(responsed)
    }
}
export const authAPI = {
    getAuth() {
        return instance.get('auth/me').then(responsed)
    },
    postLogin(email, password, rememberMe = false) {
        return instance.post('auth/login', { email, password, rememberMe }).then(responsed)
    },
    delLogin() {
        return instance.delete('auth/login').then(responsed)
    }
}
